// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import bus from './plugins/bus'
import { Input, Form, FormItem, List, ListItem, Rate } from 'view-design'
// import ViewUI from 'view-design'
import 'view-design/dist/styles/iview.css'

Vue.config.productionTip = false

Vue.use(bus)
// Vue.use(ViewUI)
Vue.component('i-input', Input)
Vue.component('i-form', Form)
Vue.component('i-form-item', FormItem)
Vue.component('i-list', List)
Vue.component('i-rate', Rate)
Vue.component('i-list-item', ListItem)
// Vue.component('i-date-picker', DatePicker)

const history = window.sessionStorage
history.clear()
let historyCount = history.getItem('count') * 1 || 0
history.setItem('/', 0)

router.beforeEach(function (to, from, next) {
  const toIndex = history.getItem(to.path)
  const fromIndex = history.getItem(from.path)
  let direction

  if (toIndex) {
    if (!fromIndex || parseInt(toIndex, 10) > parseInt(fromIndex, 10) || (toIndex === '0' && fromIndex === '0')) {
      direction = 'forward'
    } else {
      direction = 'reverse'
    }
  } else {
    ++historyCount
    history.setItem('count', historyCount)
    to.path !== '/' && history.setItem(to.path, historyCount)
    direction = 'forward'
  }

  console.log('direction', direction)
  // Vue.$bus.$emit('updateDirection', direction)
  Vue.prototype.$bus.$emit('updateDirection', direction)

  // 判断是否是ios左滑返回 或者 右滑前进
  // if (toIndex && toIndex !== '0' && !isPush && (((Date.now() - endTime) < 377) || isTouchStart)) {
  //   store.commit('updateDirection', {direction: ''})
  // } else {
  //   store.commit('updateDirection', { direction: direction })
  // }
  // isTouchStart = false

  if (/\/http/.test(to.path)) {
    let url = to.path.split('http')[1]
    window.location.href = `http${url}`
  } else {
    next()
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



