<template>
  <div :class="{'menu-wrap': true, unfold: unfoldShadow, fold: !unfoldShadow}">
    <div :class="{'icon-menu': true, 'icon-right': unfoldShadow}" @click="onUnfold"></div>
    <div class="menu-list">
      <div class="item" @click="onItem(menu)" v-for="menu in menus" :key="menu.path">{{menu.title}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    menus: {
      type: Array,
      default: () => {
        return [{
          title: 'Profile',
          path: '/profile'
        }]
      }
    },
    // 展开菜单列表
    unfold: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      unfoldShadow: false
    }
  },
  mounted () {
    this.unfoldShadow = this.unfold
  },
  methods: {
    onUnfold () {
      this.unfoldShadow = !this.unfoldShadow
      this.$emit('update:unfold', this.unfoldShadow)
    },
    onItem (menu) {
      this.onUnfold()
      this.$router.push(menu.path)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.menu-wrap {
  position: fixed;
  right: 0;
  top: 0;
  transition: all ease 500ms;
  will-change: transform;
  display: flex;
  align-items: flex-start;
}
.unfold {
  transform: translateX(0)
}
.fold {
  transform: translateX(100px)
}
.icon-menu {
  width: 30px;
  height: 30px;
  background: url(~@/assets/icons/menu.png) no-repeat center / cover;
}
.icon-right {
  .icon-menu;
  background: url(~@/assets/icons/right.png) no-repeat center / 80%;
}
.menu-list {
  box-shadow: 0 -2px 2px 2px rgba(0, 0, 0, .2);
  background: rgba(254, 254, 254, .2);
  color: #222;
  width: 100px;
  .item {
    padding: 10px;
    border-bottom: solid 1px #999;
    &:last-child {
      border-bottom: none;
    }
  }
}
</style>



