<template>
  <div class="level-up-wrap">
    <h1>Feedback</h1>
    <div class="item">
      <div class="desc">1. How do you feel about this visit?</div>
      <div>
        <i-rate :count='10' icon="ios-heart" show-text v-model="feelScore">
          <span style="color: #f5a623">{{ feelScore }}</span>
        </i-rate>
      </div>
    </div>
    <div class="item">
      <div class="desc">2. How do you like our system?</div>
      <div>
        <i-rate :count='10' icon="ios-heart" show-text v-model="feelScore">
          <span style="color: #f5a623">{{ feelScore }}</span>
        </i-rate>
      </div>
    </div>
    <div class="item">
      <div class="desc">3. Please give us comments</div>
      <i-input type="textarea" :autosize="true" placeholder="" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'Feedback',
  data () {
    return {
      nextDate: 'month/date/year time',
      feelScore: 0
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.level-up-wrap {
  height: 100%;
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  position: relative;
}
.date-item {
  border-radius: 4px;
  width: 200px;
  padding: 10px;
  border-top: solid 1px #ccc;
  border-left: solid 1px #ccc;
  border-right: solid 1px #ccc;
  &:last-child {
    border-bottom: solid 1px #ccc;
  }
}
.item {
  margin: 10px auto;
}
</style>



