<template>
  <div id="app">
    <transition
      @after-enter="$bus.$emit('vux:after-view-enter')"
      :name="viewTransition" :css="!!direction">
        <router-view class="router-view"></router-view>
      </transition>
    <left-menu :unfold="false" :menus="menus"></left-menu>
  </div>
</template>

<script>
import LeftMenu from '@/components/LeftMenu'
export default {
  name: 'App',
  components: {
    LeftMenu
  },
  data () {
    return {
      direction: 'forward',
      menus: [
        {
          title: 'Home',
          path: '/'
        },
        {
          title: 'Profile',
          path: '/profile'
        },
        {
          title: 'Level up',
          path: '/levelUp'
        },
        {
          title: 'Appointment',
          path: '/appointment'
        },
        {
          title: 'Feedback',
          path: '/feedback'
        }
      ]
    }
  },
  mounted () {
    this.$bus.$on('updateDirection', direction => {
      this.direction = direction
    })
  },
  computed: {
    viewTransition () {
      if (!this.direction) return ''
      return 'vux-pop-' + (this.direction === 'forward' ? 'in' : 'out')
    }
  }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html, body {
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}
body {
  background: url(~@/assets/bg.jpg) no-repeat center / cover;
}
h1 {
  font-size: 50px;
}
.router-view {
  width: 100%;
  /* top: 46px; */
}
#app {
  height: 100%;
}
.vux-pop-out-enter-active,
.vux-pop-out-leave-active,
.vux-pop-in-enter-active,
.vux-pop-in-leave-active {
  will-change: transform;
  transition: all 500ms;
  height: 100%;
  /* top: 46px; */
  position: absolute;
  backface-visibility: hidden;
  perspective: 1000;
}
.vux-pop-out-enter {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}
.vux-pop-out-leave-active {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.vux-pop-in-enter {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.vux-pop-in-leave-active {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}
</style>



