<template>
  <div class="level-up-wrap">
    <h1>Appiontment</h1>
    <div class="item">
      <div class="desc">Your child's next appointment:</div>
      <div>
        <i-input v-model="nextDate" />
      </div>
    </div>
    <div class="item">
      <div class="desc">Soummary of your most recent visit:</div>
      <div>
        <i-input type="textarea" :autosize="true" placeholder="" />
      </div>
    </div>
    <div class="item">
      <div class="desc">Earlier visits</div>
      <div v-for="i in 3" :key="i" class="date-item">Date{{i}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Appiontment',
  data () {
    return {
      nextDate: 'month/date/year time'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.level-up-wrap {
  height: 100%;
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  position: relative;
}
.date-item {
  border-radius: 4px;
  width: 200px;
  padding: 10px;
  border-top: solid 1px #ccc;
  border-left: solid 1px #ccc;
  border-right: solid 1px #ccc;
  &:last-child {
    border-bottom: solid 1px #ccc;
  }
}
.item {
  margin: 10px auto;
}
</style>



